import State from "./State";
import { STATE_TYPE, EVENTS } from "../consts";
import { FlickingContext, StateType, ValueOf } from "../types";

class DraggingState extends State {
  public readonly type = STATE_TYPE.DRAGGING;
  public readonly holding = true;
  public readonly playing = true;

  public onEnter(prevState: State): void {
    super.onEnter(prevState);
    this.delta = 0;
  }

  public onChange(e: any, { moveCamera, transitTo }: FlickingContext): void {
    if (!e.delta.flick) {
      return;
    }

    moveCamera(e)
      .onStopped(() => {
        transitTo(STATE_TYPE.DISABLED);
      });
  }

  public onRelease(e: any, context: FlickingContext): void {
    const { flicking, viewport, triggerEvent, moveToPanel, castToReadonlyPanel, transitTo, stopCamera } = context;
    const delta = this.delta;
    const options = flicking.options;
    const isNext = delta > 0;
    const swipeDistance = Math.abs(delta);
    const swipeAngle = e.inputEvent.deltaX
      ? Math.abs(180 * Math.atan(e.inputEvent.deltaY / e.inputEvent.deltaX) / Math.PI)
      : 90;
    const overThreshold = (swipeDistance >= options.threshold)
      && (options.horizontal
        ? swipeAngle <= options.thresholdAngle
        : swipeAngle > options.thresholdAngle);

    triggerEvent(EVENTS.HOLD_END, e, true);

    if (!overThreshold && this.targetPanel) {
      // Interrupted while animating
      viewport.moveTo(this.targetPanel, e);
      transitTo(STATE_TYPE.ANIMATING);
      return;
    }

    const currentPanel = viewport.getCurrentPanel();
    const hangerPosition = viewport.getCameraPosition() + viewport.getHangerPosition();
    const halfGap = options.gap / 2;

    // Minimum distance needed to decide prev/next panel as nearest
    /*
    * |  Prev  |     Next     |
    * |--------|--------------|
    * [][      |<-Anchor    ][] <- Panel + Gap
    */
    let minimumDistanceToChange = isNext
      ? currentPanel.getSize() - currentPanel.getRelativeAnchorPosition() + halfGap
      : currentPanel.getRelativeAnchorPosition() + halfGap;
    minimumDistanceToChange = Math.max(minimumDistanceToChange, options.threshold);

    const flick = Math.abs(e.delta.flick);
    const snap = options.snap;
    const durationOption = options.duration;
    let duration: number | undefined;
    let panelToMove = castToReadonlyPanel(currentPanel);

    if (overThreshold) {
      let count = 0;

      if (minimumDistanceToChange <= flick) {
        const position = panelToMove.position;

        while (Math.abs(panelToMove.position - position) < flick && count < snap) {
          const nextPanel = isNext ? panelToMove.next() : panelToMove.prev();

          if (!nextPanel) {
            break;
          }
          panelToMove = nextPanel;
          ++count;
        }
        count > 1 && (duration = Math.min(durationOption * count, Math.max(e.duration, durationOption)));
      }
      if (count <= 1) {
        if (swipeDistance <= minimumDistanceToChange) {
          let adjacentPanel = isNext
            ? currentPanel.getNextPanel()
            : currentPanel.getPrevPanel();

          if (options.circular) {
            const firstClonedPanel = currentPanel.getIdenticalPanels()[1];
            const lapped = Math.abs(currentPanel.getAnchorPosition() - hangerPosition)
              > Math.abs(firstClonedPanel.getAnchorPosition() - hangerPosition);

            if (lapped) {
              adjacentPanel = isNext
                ? firstClonedPanel.getNextPanel()
                : firstClonedPanel.getPrevPanel();
            }
          }
          panelToMove = castToReadonlyPanel((adjacentPanel != null) ? adjacentPanel : currentPanel);
        } else {
          panelToMove = castToReadonlyPanel(viewport.findNearestPanel());
        }
      }
    } else if (options.circular) {
      // Restore case
      const firstClonedPanel = currentPanel.getIdenticalPanels()[1];
      const lapped = Math.abs(currentPanel.getAnchorPosition() - hangerPosition)
        > Math.abs(firstClonedPanel.getAnchorPosition() - hangerPosition);

      if (!isNext && lapped) {
        panelToMove = castToReadonlyPanel(firstClonedPanel);
      }
    }

    const eventType = (!overThreshold || (!options.circular && panelToMove.position === currentPanel.getPosition()))
      ? EVENTS.RESTORE
      : EVENTS.CHANGE;

    moveToPanel(panelToMove, eventType, e, duration)
      .onSuccess(() => {
        transitTo(STATE_TYPE.ANIMATING);
      })
      .onStopped(() => {
        transitTo(STATE_TYPE.DISABLED);
        stopCamera(e);
      });
  }
}

export default DraggingState;
